class BondingCurve {
    constructor(initialPrice, slope, buyFeePercent = 0, sellFeePercent = 0) {
        this.initialPrice = initialPrice; //  
        this.slope = slope; //  
        this.totalSupply = 0; //   
        this.totalBought = 0; //    
        this.totalSold = 0; //    
        this.totalUSDTSpent = 0; //   USDT,    
        this.totalUSDTReceived = 0; //   USDT,    
        this.buyFeePercent = buyFeePercent; //    
        this.sellFeePercent = sellFeePercent; //    
        this.priceHistory = []; //    
    }

    //         
    getPrice(supply) {
        return this.initialPrice + this.slope * supply; //  =   +   * 
    }

    //    
    buyTokens(amount) {
        let totalCost = 0;
        for (let i = 0; i < amount; i++) {
            totalCost += this.getPrice(this.totalSupply + i);
        }
        const fee = totalCost * (this.buyFeePercent / 100);
        totalCost += fee; //     
        this.totalSupply += amount; //    
        this.totalBought += amount; //     
        this.totalUSDTSpent += totalCost; //    USDT,   
        this.priceHistory.push({ volume: this.totalBought, price: this.getPrice(this.totalSupply) }); //    
        return totalCost; //      
    }

    //    
    sellTokens(amount) {
        let totalRevenue = 0;
        for (let i = 0; i < amount; i++) {
            totalRevenue += this.getPrice(this.totalSupply - i - 1);
        }
        const fee = totalRevenue * (this.sellFeePercent / 100);
        totalRevenue -= fee; //     
        this.totalSupply -= amount; //    
        this.totalSold += amount; //     
        this.totalUSDTReceived += totalRevenue; //    USDT,   
        this.priceHistory.push({ volume: this.totalBought, price: this.getPrice(this.totalSupply) }); //    
        return totalRevenue; //       
    }

    //               
    getNetTokensAndValue() {
        const netTokens = this.totalBought - this.totalSold;
        const currentValue = netTokens * this.getPrice(this.totalSupply);
        return { netTokens, currentValue };
    }

    //      USDT,   
    getUSDTBalance() {
        const netUSDT = this.totalUSDTSpent - this.totalUSDTReceived;
        return { totalUSDTSpent: this.totalUSDTSpent, totalUSDTReceived: this.totalUSDTReceived, netUSDT };
    }
}

export default BondingCurve;